<template>
  <div class="app-container">
    <div class="filter-container">
      <el-input
        v-model="listQuery.name"
        placeholder="名称"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        @click="handleFilter"
      >
        搜索
      </el-button>
      <el-button
        class="filter-item"
        style="margin-left: 10px;"
        type="primary"
        icon="el-icon-edit"
        @click="handleCreate"
      >
        添加
      </el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      border
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="ID" width="75">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column label="名称" align="center">
        <template slot-scope="scope">
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label="编码" align="center">
        <template slot-scope="scope">
          {{ scope.row.code }}
        </template>
      </el-table-column>
      <el-table-column class-name="status-col" label="状态" align="center">
        <template slot-scope="scope">
          {{ statusMap[scope.row.status] }}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="{ row }">
          <el-button type="primary" size="mini" @click="handleUpdate(row)">
            编辑
          </el-button>
          <el-button size="mini" @click="handleModifyStatus(row)">
            {{ 0 === row.status ? "启用" : "停用" }}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="accessPointType"
        label-position="left"
        label-width="50px"
        style="margin-left:35px;"
      >
        <el-form-item label="名称">
          <el-input v-model="accessPointType.name" />
        </el-form-item>
        <el-form-item label="编码">
          <el-input v-model="accessPointType.code" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">
          取消
        </el-button>
        <el-button
          type="primary"
          @click="dialogStatus === 'create' ? createData() : updateData()"
        >
          保存
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { accessType, createType, updateType } from "@/api/accessPoint";
import Pagination from "@/components/Pagination";
import waves from "@/directive/waves"; // waves directive

export default {
  components: { Pagination },
  directives: { waves },
  filters: {
    priceFilter(price) {
      let val = "success";
      if (price === 0) {
        val = "warning";
      } else if (price < 0) {
        val = "danger";
      }
      return val;
    }
  },
  data() {
    return {
      total: 0,
      listQuery: {
        name: "",
        code: "",
        pageNo: 1,
        pageSize: 20
      },
      accessPointType: {
        id: undefined,
        code: "",
        name: "",
        status: "1"
      },
      list: null,
      listLoading: true,
      textMap: {
        update: "编辑",
        create: "创建"
      },
      statusMap: {
        1: "正常",
        0: "停用"
      },
      dialogFormVisible: false,
      rules: {
        name: [
          { required: true, message: "name is required", trigger: "change" }
        ],
        code: [
          { required: true, message: "code is required", trigger: "change" }
        ]
      },
      dialogStatus: ""
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.listLoading = true;
      accessType(this.listQuery).then(response => {
        if (response.code === 0) {
          this.list = response.data.record;
          this.total = response.data.total;
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
        }
        this.listLoading = false;
      });
    },
    handleFilter() {
      this.fetchData();
    },
    resetTemp() {
      this.accessPointType = {
        id: undefined,
        code: "",
        name: "",
        status: 1
      };
    },
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    handleUpdate(row) {
      this.accessPointType.id = row.id;
      this.accessPointType.name = row.name;
      this.accessPointType.code = row.code;
      this.accessPointType.status = row.status;
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    handleModifyStatus(row) {
      this.accessPointType.status = row.status === 0 ? 1 : 0;
      this.accessPointType.id = row.id;
      this.accessPointType.name = row.name;
      this.accessPointType.code = row.code;
      this.listLoading = true;
      updateType(this.accessPointType).then(response => {
        if (response.code === 0) {
          row.status = this.accessPointType.status;
          this.$message({
            message: "操作成功",
            type: "success"
          });
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
          row.status = row.status === 0 ? 1 : 0;
        }
        this.listLoading = false;
      });
    },
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          this.listLoading = true;
          createType(this.accessPointType).then(response => {
            if (response.code === 0) {
              this.accessPointType.id = response.data;
              this.list.unshift(this.accessPointType);
              this.dialogFormVisible = false;
              this.$notify({
                title: "成功",
                message: "创建成功",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
            this.listLoading = false;
          });
        }
      });
    },
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          this.listLoading = true;
          updateType(this.accessPointType).then(response => {
            if (response.code === 0) {
              for (const v of this.list) {
                if (v.id === this.accessPointType.id) {
                  const index = this.list.indexOf(v);
                  this.list.splice(index, 1, this.accessPointType);
                  break;
                }
              }
              this.dialogFormVisible = false;
              this.$notify({
                title: "成功",
                message: "更新成功",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
            this.listLoading = false;
          });
        }
      });
    }
  }
};
</script>
